import React, { useState } from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";

// Style
import "../css/Login.css";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const [localToken, setToken] = useState("");
  const [localUserID, setUserID] = useState("");

  function validateForm() {
    return email.length > 0 && password.length > 0;
  }

  function handleSubmit(event: any) {
    event.preventDefault();
    const apiAdress: string = "http://localhost:3000/api/auth/login";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email: email, password: password }),
    };

    fetch(apiAdress, requestOptions)
      .then((res) => {
        console.log(res.status);
        if (res.status !== 200) {
          console.log("error");
        }
        console.log(res);
        return res.json();
      })
      .then(
        (result) => {
          console.log("result : ");
          console.log(result);
          if (result.error) {
            if (typeof result.error.message === "string") {
              setError(result.error.message);
            } else {
              setError("Error without message.");
            }
          } else {
            setError("");
          }
          if (result.token) {
            setToken(result.token);
          }
          if (result.userId) {
            setUserID(result.userId);
          }
        },
        (error) => {
          console.log("Error : " + error.error);
        }
      );
  }

  const authHeader = function() {
    return "Bearer " + localToken;
  };

  const getStuff = () => {
    const apiAdress: string = "http://localhost:3000/api/stuff";
    const requestOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        authorization: authHeader(),
        userId: localUserID,
      },
    };

    fetch(apiAdress, requestOptions)
      .then((res) => {
        console.log(res.status);
        if (res.status !== 200) {
          console.log("error");
        }
        console.log(res);
        return res.json();
      })
      .then(
        (result) => {
          console.log("result : ");
          console.log(result);
          if (result.error) {
            if (typeof result.error.message === "string") {
              setError(result.error.message);
            } else {
              setError("Error without message.");
            }
          }
        },
        (error) => {
          console.log("Error : " + error);
        }
      );
  };

  return (
    <div className="LoginContainer">
      <Form onSubmit={handleSubmit} className="LoginFormBox">
        <Form.Group controlId="email">
          <Form.Label>Email</Form.Label>
          <Form.Control
            autoFocus
            type="email"
            value={email}
            placeholder="foo@mail.com"
            onChange={(e: any) => setEmail(e.target.value)}
          />
        </Form.Group>
        <Form.Group controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            value={password}
            placeholder=". . ."
            onChange={(e: any) => setPassword(e.target.value)}
          />
        </Form.Group>
        <Button
          block
          size="lg"
          type="submit"
          className={
            "Login " + (validateForm() ? "btn-enabled" : "btn-disabled")
          }
          disabled={!validateForm()}
        >
          Get token
        </Button>
        {error ? (
          <div>
            <br></br> {error}{" "}
          </div>
        ) : (
          <div>
            <br></br>{" "}
          </div>
        )}
      </Form>

      <Button onClick={getStuff}> Get stuff</Button>
    </div>
  );
}
