import React, { useState } from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";

// ---------Style----------
import "../css/Login.css";

export default function SignUp() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  function validateForm() {
    return email.length > 0 && password.length > 0;
  }

  function handleSubmit(event: any) {
    event.preventDefault();
    const apiAdress: string = "http://localhost:3000/api/auth/signup";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email: email, password: password }),
    };

    fetch(apiAdress, requestOptions)
      .then((res) => {
        console.log(res.status);
        if (res.status !== 200) {
          console.log("error");
        }
        console.log(res);
        return res.json();
      })
      .then(
        (result) => {
          console.log("result : ");
          console.log(result);
          if (result.error) {
            if(result.error.message){
              setError(result.error.message);
            }
          }
          if (result.message) {
            setError(result.message);
          }
        },
        (error) => {
          console.log("Error : " + error.error);
        }
      );
  }

  return (
    <div className="LoginContainer">
      <Form onSubmit={handleSubmit} className="LoginFormBox">
        <h2 className="signupTitle"> Sign up</h2>
        <Form.Group controlId="email">
          <Form.Label>Email</Form.Label>
          <Form.Control
            autoFocus
            type="email"
            value={email}
            placeholder="foo@mail.com"
            onChange={(e: any) => setEmail(e.target.value)}
          />
        </Form.Group>
        <Form.Group controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            value={password}
            placeholder=". . ."
            onChange={(e: any) => setPassword(e.target.value)}
          />
        </Form.Group>
        <Button
          block
          size="lg"
          type="submit"
          className={
            "Login " + (validateForm() ? "btn-enabled" : "btn-disabled")
          }
          disabled={!validateForm()}
        >
          Sign up
        </Button>
        {error ? (
          <div>
            <br></br> {error}{" "}
          </div>
        ) : (
          <div>
            <br></br>{" "}
          </div>
        )}
      </Form>
    </div>
  );
}
