import React from "react";
import { BrowserRouter as Router, Switch, Route, NavLink } from "react-router-dom";
import { Navbar, Nav} from "react-bootstrap";
import logo from "./assets/logo.svg";

// Styles
import "./css/App.css";
import "bootstrap/dist/css/bootstrap.min.css";

// Components

import Game from "./components/tictactoe";
import Login from "./components/Login";
import SignUp from "./components/signup";
import Home from "./components/Home";
import FetchExample from "./components/fetch_component_example";
import postExample from "./components/postExample";
import authAndGet from "./components/authAndGet";

interface logoProps {
  className?: string;
}

function MyLogo({ className: classname = "" }: logoProps) {
  return <img src={logo} className={"App-logo " + classname} alt="logo" />;
}

export default function App() {

  return (
    <div className="thisApp">
      <Router>
        <div>
          <Navbar bg="dark" variant="dark">
            <Navbar.Brand href="/">
              <MyLogo />
            </Navbar.Brand>
            {/* <Navbar.Toggle aria-controls="responsive-navbar-nav" /> */}
            <Nav defaultActiveKey="/" variant="pills">
              <Nav.Link as={NavLink} exact activeClassName="active" to="/">Home</Nav.Link>
              <Nav.Link as={NavLink} exact activeClassName="active" to="morpion">Morpion</Nav.Link>
              <Nav.Link as={NavLink} exact activeClassName="active" to="login">Login</Nav.Link>
              <Nav.Link as={NavLink} exact activeClassName="active" to="authAndGet">auth Get</Nav.Link>
              <Nav.Link as={NavLink} exact activeClassName="active" to="signup">Sign up</Nav.Link>
              <Nav.Link as={NavLink} exact activeClassName="active" to="fetch">Fetch</Nav.Link>
              <Nav.Link as={NavLink} to="post">Post</Nav.Link>

              <MyLogo className="mooving-logo" />
              {/* <Logo /> */}
            </Nav>
          </Navbar>
        </div>
        <div>
          <Switch>
            <Route path="/morpion" component={Game} />
            <Route path="/login" component={Login} />
            <Route path="/signup" component={SignUp} />
            <Route path="/authAndGet" component={authAndGet} />
            <Route path="/fetch" component={FetchExample} />
            <Route path="/post" component={postExample} />
            <Route exact path="/" component={Home} />
          </Switch>
        </div>
        <div > </div>
        <footer> </footer>
      </Router>
    </div>
  );
}
